<template>
  <div class="signup-page">
    <transition name="fade" mode="out-in">
      <div class="main-login" v-if="signupStep == 0">
        <div class="welcome-back">
          Get started
          <div class="own-channel">
            Own a channel? <router-link to="/partner-signup" class="own-channel-click">Click here</router-link>
          </div>
        </div>
        <label class="form-box-label" :style="firstName.length ? 'opacity: 1; transform: translateY(0px); z-index: 2;' : ''"> First name </label>
        <input type="text" placeholder="First name" class="form-box" v-model="firstName" spellcheck="false"/>
        <label class="form-box-label" :style="lastName.length ? 'opacity: 1; transform: translateY(0px); z-index: 2;' : ''"> Last name </label>
        <input type="text" placeholder="Last name" class="form-box" v-model="lastName" spellcheck="false"/>
        <label class="form-box-label" :style="email.length ? 'opacity: 1; transform: translateY(0px); z-index: 2;' : ''"> Email </label>
        <input type="text" placeholder="Email" class="form-box" v-model="email" spellcheck="false"/>
        <label class="form-box-label" :style="confirmEmail.length ? 'opacity: 1; transform: translateY(0px); z-index: 2;' : ''"> Confirm email </label>
        <input type="text" placeholder="Confirm email" class="form-box" v-model="confirmEmail" spellcheck="false"/>
        <label class="form-box-label" :style="creatorCode.length ? 'opacity: 1; transform: translateY(0px); z-index: 2;' : ''"> Creator code (optional) </label>
        <input type="text" placeholder="Creator code (optional)" class="form-box" v-model="creatorCode" spellcheck="false"/>
        <div class="wrong-login" id="wrong-login"> 
          Please enter a valid email 
        </div>
        <div class="full-width">
          <button class="sign-in" @click="signIn" id="login-button" :style="email.length && firstName.length && lastName.length && confirmEmail.length ? 'opacity: 1' : ''"> 
            <span id="sign-up-text">Sign up</span>
            <div class="loader" id="login-loader"/>
          </button>
        </div>
      </div>
      <div class="setup-complete" v-else-if="signupStep == 1">
        <!--Congratulations! Your account is being verified. You will receive an email now about how to finish the setup process.-->
        Sorry. We are currently receiving a large number of new users, and your account could not be created at this time. We'll email you when new spots become available. 
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  data(){
    return{
      firstName: '',
      lastName: '',
      email: '',
      confirmEmail: '',
      creatorCode: '',
      signupStep: 0,
    }
  },
  mounted(){
    document.getElementsByClassName('footer')[0].style.display = 'none';
    document.getElementsByClassName('mobile-logo')[0].style.display = 'none';
    document.getElementsByClassName('navbar-link').forEach(element => {
      element.style.display = 'none';
    });
    document.getElementsByClassName('navbar-link-signup')[0].style.display = 'none';
    document.getElementById('navbar-login-button').style.display = 'block';
    document.getElementById('desktop-navigation-links').style.display = 'block';
    document.getElementsByClassName('navigation-drop')[0].style.display = 'none';
    document.getElementById('navbar-border').style.borderBottom = '1px solid white';
  },
  unmounted(){
    document.getElementsByClassName('footer')[0].style.display = 'flex';
    document.getElementsByClassName('mobile-logo')[0].style.display = '';
    document.getElementsByClassName('navbar-link').forEach(element => {
      element.style.display = '';
    });
    document.getElementsByClassName('navbar-link-signup')[0].style.display = '';
    document.getElementById('desktop-navigation-links').style.display = '';
    document.getElementsByClassName('navigation-drop')[0].style.display = '';
    document.getElementById('navbar-border').style.borderBottom = '';
  },
  methods: {
    signIn: function(){
      document.getElementById('login-loader').style.display = 'inline-block';
      document.getElementById('sign-up-text').innerText = '';
      document.getElementById('wrong-login').style.opacity = 0; 
      setTimeout(() => {
        document.getElementById('login-loader').style.display = 'none';
        document.getElementById('sign-up-text').innerText = 'Sign up';
        if(!this.email.includes('@') || !this.email.includes('.') || this.email.length < 5){
          document.getElementById('wrong-login').innerText = 'Please enter a valid email';
          document.getElementById('wrong-login').style.opacity = 1;
        }
        else if(this.email != this.confirmEmail){
          document.getElementById('wrong-login').innerText = 'Emails do not match';
          document.getElementById('wrong-login').style.opacity = 1;
        }
        else{
          const options = {
            method: 'post',
            body: JSON.stringify({first:this.firstName, last: this.lastName, email: this.email, creatorCode: this.creatorCode, messageType: 'User Sign Up'}),
            headers: {'Content-Type': 'application/json'}
          };
          fetch('/api/mail', options);
          this.signupStep = 1;
        }  
      } , 1000);
    }
  }
}
</script>

<style scoped>
  .signup-page{
    padding: 180px 30px;
  }
  .main-login{
    text-align: left;
    width: 100%;
    max-width: 432px;
    margin: 0 auto;
  }
  .welcome-back{
    font-size: 24px;
    margin-bottom: 48px;
    font-weight: 600;
  }
  .own-channel{
    font-size: 16px;
    color: var(--light-gray);
    margin-top: 12px;
  }
  .own-channel-click{
    color: var(--primary-color);
    text-decoration: none;
  }
  .form-box{
    border: none;
    border-bottom: 2px solid var(--lightest-gray);
    margin-bottom: 32px;
    font-size: 16px;
    font-family: inherit;
    padding: 7px 0;
    width: 100%;
    font-weight: 600;
    background: transparent;
  }
  .form-box:focus{
    outline: none;
    border-bottom: 2px solid black;
  }
  .form-box-label{
    opacity: 0;
    transition: .3s all;
    transform: translateY(20px);
    text-align: left;
    font-size: 12px;
    z-index: -2;
    color: var(--light-gray);
    width: 100%;
  }
  .wrong-login{
    width: 60%;
    border: 1px solid red;
    background: #fceae9;
    opacity: 0;
    transition: .3s all;
    padding: 10px 35px;
    border-radius: 4px;
  }
  .full-width{
    width: 100%;
    text-align:end;
    margin-top: 24px;
  }
  .sign-in{
    color: white;
    background: var(--primary-color);
    padding: 10px 35px;
    transition: .5s all;
    border-radius: 20px;
    opacity: 0;
    border: none;
    font-family: inherit;
    font-size: 16px;
    width: 125px;
  }
  .sign-in:hover{
    background: var(--primary-color-light);
  }
  .loader {
    border: 3px solid #f3f3f3; /* Light grey */
    border-top: 3px solid var(--primary-color-light);
    border-radius: 50%;
    width: 10px;
    height: 10px;
    animation: spin 1s ease-in-out infinite;
    display: none;
    margin: 0 auto;
  }
  .setup-complete{
    width: 100%;
    max-width: 500px;
    text-align: center;
    font-size: 20px;
    margin: 0 auto;
    line-height: 30px;
  }
  .fade-enter-active,
  .fade-leave-active {
    transition: opacity 0.5s ease;
  }

  .fade-enter-from,
  .fade-leave-to {
    opacity: 0;
  }
  /* Safari */
  @-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
  }
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
</style>